const Section = styled.div`
  margin-top: 5%;

  .glav {
    font-size: 48px;
    font-weight: bold;
    color: black;
    text-align: center;
  }

  .item {
    padding-top: 5%;
    margin: 0 auto;
    .left-part {
      padding-top: 10%;
      display: grid;
      grid-gap: 20px;
      height: 100%;
      .teachers {
        font-size: 24px;
        color: #ffa900;
      }
      .teacher {
        font-size: 28px;
        color: #0057ff;
        font-weight: bold;
      }
      .mini-bars {
        .mini-bar {
          display: flex;
          img {
            max-width: 32px;
            max-height: 32px;
          }
          p {
            width: 100%;
            padding-left: 2%;
            font-size: 18px;
            font-weight: 400;
            span {
              font-weight: bold;
            }
          }
        }
      }
    }
    .right-part {
      .backgrounder {
        max-width: 440px;
        max-height: 530px;
        background-color: #ffa900;
        border-radius: 25px;
        img {
          max-width: 373.73px;
          max-height: 539px;
          margin: 0 auto;
        }
      }
    }
  }

  .owl-theme .owl-dots .owl-dot span {
    background-color: #ffa900;
  }

  @media (min-width: 500px) and (max-width: 992px) {
    .item {
      width: 70%;
      .left-part {
        order: 2;
      }
      .right-part {
        order: 1;
        .backgrounder {
          margin: 0 auto;
        }
      }
    }
  }

  @media all and (max-width: 500px) {
    margin-top: 10% !important;

    .glav {
      font-size: x-large;
    }

    .item {
      width: 77%;
      .left-part {
        order: 2;
        .mini-bars {
          .mini-bar {
            p {
              font-size: 15px;
              padding-left: 5%;
            }
          }
        }
      }
      .right-part {
        order: 1;
        .backgrounder {
          height: 100%;
          img {
            width: 100%;
          }
        }
      }
    }
  }
`;

export default SectionOne;
